.main {
  &__inner {
    position: relative;
  }
  .swiper-slide {
    padding: 420px 0 20px 0;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% auto;
  }
  &__slide-content {
    background: linear-gradient(110.01deg, rgba(36, 60, 160, 0.89) 1.98%, rgba(26, 49, 140, 0.89) 114.82%);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.27);
    border-radius: 30px;
    padding: 50px 10px 56px;
    max-width: 970px;
    margin: 0 auto;
    text-align: center;
  }
  &__slide-title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.025em;
    color: #FFFFFF;
    span {
      color: #D64923;
    }
  }
  &__slide-btn {
    width: 302px;
    height: 64px;
    margin: 25px auto 0;
    font-size: 22px;
    font-weight: 700;
    border-radius: 7px;
    text-transform: uppercase;
  }
  .swiper-pagination {
    bottom: 32px;
    z-index: 1;
  }
  .swiper-pagination-bullet {
    width: 22px;
    height: 22px;
    background: rgba(255, 255, 255, 0.5);
    &-active {
      background:  #FFFFFF;
    }
  }
}
.equipment-estate {
  padding: 110px 0 30px;
  overflow: hidden;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__search {
    position: relative;
    margin-left: 15px;
    img {
      position: absolute;
      top: 50%;
      left: 10px;
      transform: translate(0, -50%);
    }
    input {
      width: 495px;
      height: 40px;
      font-size: 16px;
      line-height: 120%;
      color: rgba(36, 60, 160, 1);
      background: #FFFFFF;
      border: 1px solid #243CA0;
      box-shadow: inset 1px -2px 9px rgba(0, 0, 0, 0.1);
      border-radius: 3px;
      padding-left: 35px;
      &::placeholder {
        color: rgba(36, 60, 160, 0.4);
      }
    }
  }
  &__content {
    background: #FFFFFF;
    box-shadow: 0px 4px 17px rgba(12, 8, 149, 0.1);
    border-radius: 20px;
    padding: 20px 20px 40px;
    margin-top: 70px;
  }
  &__btn {
    font-weight: 500;
    width: 167px;
    height: 40px;
    margin-top: -30px;
    border-radius: 3px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    gap: 25px;
  }
}
