.boards {
  background: rgba(63, 100, 255, 0.1);
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    margin-left: 50px;
  }
  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }
  &__desc {
    max-width: 490px;
    margin-left: 50px;
  }
  &__text {
    margin-top: 25px;
  }
  &__btns {
    margin-top: 25px;
  }
  &__btn {
    font-weight: 700;
    width: 251px;
    height: 55px;
    font-size: 20px;
    text-transform: uppercase;
    border-radius: 7px;
    &:not(:first-child) {
      margin-top: 20px;
    }
  }
  &__card {
    background: #FFFFFF;
    box-shadow: 0px 2.95945px 12.5777px rgba(12, 8, 149, 0.1);
    border-radius: 8.64129px;
    text-align: center;
    padding: 5px 10px;
    display: block;
    &:not(:first-child) {
      margin-top: 15px;
    }
    &:hover {
      background: #243CA0;
      box-shadow: -0.739863px 2.21959px 29.5945px rgba(90, 96, 240, 0.68);
      .boards__card-img {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%, 0);
          width: 110px;
          height: 100%;
          filter: blur(40.6925px);
          background: #637CE4;
        }
      }
      .boards__card-title {
        color: #FFFFFF;
      }
    }
    &-img {
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      img {
        max-width: 100%;
        max-height: 100%;
        position: relative;
        z-index: 2;
      }
    }
    &-title {
      margin-top: 15px;
      font-size: 14px;
      line-height: 110%;
      font-weight: 500;
      letter-spacing: -0.025em;
      color: #1C1C1C;


    }
  }
}
