.hits {
  overflow: hidden;
  padding: 50px 0 200px;
  .swiper {
    overflow: unset;
    &-slide{
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
      &-active,
      &-next,
      &-next+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__slide {
    position: relative;
    margin-top: 30px;
  }
  .swiper-pagination {
    bottom: -100px;
  }
  .swiper-pagination-bullet {
    width: 22px;
    height: 22px;
    background: rgba(36, 60, 160, 0.4);
    &-active {
      background: #243CA0;
    }
  }
}
