@media screen and (max-width: 992px) {
  .catalog-item {
    &__inner {
      grid-template-columns: 35% auto;
      gap: 50px;
    }
  }
  .directions {
    padding: 0 0 50px 0;
    &__cards {
      grid-template-columns: 1fr;
      gap: 20px;
    }
    &__item {
      grid-template-rows: unset;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
    &__card {
      &-blue,
      &-blue2,
      &-blue3,
      &-blue4,
      &-blue5,
      &-blue6,
      &-red,
      &-yellow {
        padding: 50px 15px;
      }
      padding: 50px 15px;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog-item {
    padding: 0 0 20px 0;
    &__inner {
      grid-template-columns: 100%;
      gap: 50px;
    }
    &__right {
      padding-top: 0;
    }
    &__price {
      font-size: 20px;
      margin-top: 15px;
    }
  }
  .characteristic {
    padding: 20px 0 0 0;
    &__desc {
      font-size: 18px;
      margin-top: 20px;
      p:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .directions {
    &__item {
      grid-template-columns: 1fr;
    }
  }
}