@media screen and (max-width: 1230px) {
  .header {
    &__tel {
      margin-left: 25px;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 25px;
        }
      }
    }
  }
}

@media screen and (max-width: 1160px) {
  .header {
    &__burger {
      display: block;
      &--active {
        span {
          background: #FFFFFF;
          &:nth-child(1) {
            transform: rotate(-45deg);
            top: 50%;
          }
          &:nth-child(2) {
            display: none;
          }
          &:nth-child(3) {
            transform: rotate(45deg);
            top: 50%;
          }
        }
      }
    }
    &__menu {
      position: absolute;
      z-index: 3;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100vh;
      padding: 150px 15px 30px;
      background: #243ca0;
      transition: all 0.5s;
      flex-direction: column;

      &--active {
        left: 0;
      }
    }
    &__links {
      border-bottom: none;
      padding-bottom: 0;
    }
    &__search {
      margin-top: 0;
    }
    &__nav {
      display: block;
      overflow-y: auto;
      height: 100%;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #000;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #FFFFFF;
        border-radius: 3px;
      }
    }
    &__list {
      display: block;
      li {
        color: #FFFFFF;
        a:hover {
          color: #bababa;
        }
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
      }
    }
    &__btn {
      margin: 30px 0 0 0;
      color: #FFFFFF;
      &:after {
        background: #FFFFFF;
      }
    }
    &__mail {
      span {
        display: none;
      }
    }
    &__tel {
      &--two {
        display: none;
      }
      span {
        display: none;
      }
    }
    &__catalog {
      &-hidden {
        position: static;
      }
    }
    &__hidden-inner {
      grid-template-columns: 1fr;
      gap: 15px;
      background: unset;
      box-shadow: unset;
      max-width: 100%;
      margin: 0;
      padding: 20px 0 0 20px;
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__logo {
      width: 100px;
      height: auto;
    }
    &__search {
      position: absolute;
      z-index: 5;
      top: -300%;
      left: 0;
      width: 100%;
      height: 100vh;
      padding: 150px 15px 30px;
      background: #FFFFFF;
      transition: all 0.5s;
      flex-direction: column;
      margin-left: 0;
      &--active {
        top: 0;
      }
      &-inner {
        input {
          width: 100%;
        }
      }
    }
    &__btn-search {
      display: block;
      &--active {
        background: url("../img/close.svg");
        width: 32px;
        height: 32px;
        position: absolute;
        top: 30px;
        right: 25px;
      }
    }
    &__burger {
      width: 28px;
      height: 19px;
      span {
        height: 2px;
      }
    }
  }
}