.display2 {
  font-weight: 700;
  font-size: 30px;
  line-height: 120%;
  letter-spacing: -0.025em;
  color: #3A3A3A;
}
.btn-red {
  background: linear-gradient(90.53deg, #D64923 0.57%, #EB5B51 99.74%);
  line-height: 120%;
  color: #FFFFFF;
  &:hover {
    filter: drop-shadow(0px 6px 25px rgba(255, 210, 185, 0.5));
  }
  &:focus {
    box-shadow: inset 4px -5px 3px rgba(0, 0, 0, 0.13);
  }
}
.btn-border {
  line-height: 120%;
  letter-spacing: -0.025em;
  color: #D64923;
  border: 2px solid #D64923;
  background: transparent;
  &:hover {
    background: linear-gradient(90deg, #D64923 0%, #EB5B53 100%);
    color: #FFFFFF;
  }
}
.equipment-card {
  display: flex;
  flex-direction: column;
  cursor: pointer;
  position: relative;
  @media screen and (min-width: 769px){
    &:before{
      content: '';
      position: absolute;
      z-index: 2;
      top: -23px;
      left: 50%;
      transform: translateX(-50%);
      width: calc(100% + 75px);
      height: calc(100% + 85px);
      background: #FFFFFF;
      box-shadow: -1px 3px 40px rgba(90, 96, 240, 0.3);
      border-radius: 3px;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &:hover {
      &:before{
        opacity: 1;
        pointer-events: unset;
      }
      .equipment-card__info {
        opacity: 1;
        pointer-events: unset;
      }
      .equipment-card__desc,
      .equipment-card__img{
        z-index: 2;
      }
    }
    &__info{
      z-index: 3;
      position: absolute;
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
    }
  }
  &__img {
    height: 190px;
    display: flex;
    flex-direction: column;
    position: relative;
    &-wrap{
      margin-top: auto;
      display: flex;
      justify-content: center;
    }
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    margin-top: auto;
    padding-top: 15px;
    text-align: center;
    position: relative;
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
  }
  &__info {
    margin-top: 7px;
    top: 100%;
    left: 0;
    width: 100%;
    text-align: center;
  }
  &__request {
    font-size: 18px;
    font-weight: 500;
    color: #243CA0;
    display: block;
  }
  &__link {
    font-size: 15px;
    text-align: center;
    color: rgba(36, 60, 160, 0.9);
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      border-bottom: 0.5px solid rgba(36, 60, 160, 0.9);
      margin-right: 5px;
    }


  }
}
.desc {
  font-size: 16px;
  line-height: 120%;
  p {
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
}
.hits-card {
  background: #FFFFFF;
  box-shadow: 0px 4px 17px rgba(12, 8, 149, 0.1);
  border-radius: 20px;
  padding: 45px 25px 20px;
  text-align: center;
  transition: all 0.5s;
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  &:hover {
    box-shadow: 0px 1px 25px rgba(36, 60, 160, 0.32);
    .hits-card__icons {
      opacity: 1;
    }
  }
  &__icons {
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    align-items: center;
    opacity: 0;
  }
  &__icon {
    background: transparent;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid #243CA0;
    &:not(:first-child) {
      margin-left: 8px;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 67%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 108.2%;
    letter-spacing: -0.025em;
    padding-top: 15px;
    margin-top: auto;
  }
  &__price {
    font-size: 24px;
    line-height: 108.2%;
    letter-spacing: -0.025em;
    color: #243CA0;
    margin-top: 15px;
  }
  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
  }
  &__btns-wrap {
    position: relative;
  }
  &__btn {
    font-weight: 500;
    width: 127px;
    height: 40px;
    border-radius: 3px;
    text-transform: uppercase;
  }
  &__btn2 {
    font-weight: 500;
    width: 155px;
    height: 40px;
    padding-left: 45px;
    position: relative;
    border-radius: 3px;
    text-transform: uppercase;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 47px;
      transform: translate(0, -50%);
      width: 0.5px;
      height: 19px;
      background: rgba(255, 255, 255, 0.58);
    }
  }
  &__calc {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 5px;
    transform: translate(0, -50%);
    input {
      width: 21px;
      height: 15px;
      font-size: 16px;
      border: none;
      background: transparent;
      color: #FFFFFF;
     text-align: center;
    }
    &-plus {
      background: transparent;
      color: #FFFFFF;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-minus {
      background: transparent;
      color: #FFFFFF;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
.news-card {
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  box-shadow: 0px 4px 12px rgba(51, 51, 153, 0.1);
  border-radius: 20px;
  padding: 25px 20px;
  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    letter-spacing: -0.025em;
    color: #3A3A3A;
    margin-top: 18px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__text {
    padding-top: 18px;
    margin-top: auto;
    font-size: 15px;
    line-height: 130%;
    text-align: justify;
    letter-spacing: -0.025em;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 18px;
  }
  &__date {
    font-size: 16px;
    line-height: 120%;
    color: #7C90A1;
  }
  &__link {
    display: flex;
    align-items: center;
    color: rgba(36, 60, 160, 0.9);
    span {
      position: relative;
      margin-right: 7px;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.5px;
        background: rgba(36, 60, 160, 0.9);
      }
    }
  }
}
.form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__text {
    font-size: 22px;
    line-height: 130%;
    letter-spacing: -0.025em;
    margin-top: 35px;
    span {
      font-weight: 500;
    }
  }
  &__btn {
    font-weight: 700;
    width: 291px;
    height: 64px;
    margin-top: 30px;
    font-size: 22.5882px;
    border-radius: 7px;
    text-transform: uppercase;
    position: relative;
    z-index: 2;
  }
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    &:not(:first-child) {
      margin-left: 40px;
    }
  }
  &-group__wrap {
    width: 293px;
    position: relative;
    &:not(:first-child) {
      margin-top: 22px;
    }
    input {
      width: 100%;
      height: 64px;
      background: #FFFFFF;
      border: 1px solid #243CA0;
      border-radius: 7px;
      padding: 0 10px 0 40px;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: -0.025em;
      color: #000;
      &::placeholder {
        color: #7C90A1;
      }
    }
    i {
      position: absolute;
      top: 50%;
      left: 10px;
      transform: translate(0, -50%);
    }
  }
  &-group__wrap-textarea {
    width: 341px;
    position: relative;
    textarea {
      width: 100%;
      height: 236px;
      background: #FFFFFF;
      border: 1px solid #243CA0;
      border-radius: 7px;
      padding: 20px 20px 20px 50px;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: -0.025em;
      resize: none;
      color: #000;
      &::placeholder {
        color: #7C90A1;
      }
    }
    i {
      position: absolute;
      top: 20px;
      left: 20px;
    }
  }
}
.breadcrumbs {
  padding: 0 0 25px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    font-size: 14px;
    line-height: 13px;
    color: #1A318C;
    margin-bottom: 15px;
    position: relative;
    span {
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.2px;
        background: #1A318C;
      }
    }
    &:not(:last-child) {
      padding-right: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 13px;
        transform: translate(0, -50%);
        width: 8px;
        height: 100%;
        background-image: url("../img/arrow-right.svg");
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
}
