@media screen and (max-width: 1400px) {
  .stages {
    &__line {
      display: none;
    }
  }
}

@media screen and (max-width: 1300px) {
  .works {
    &__slider {
      &:after {
        right: -14px;
      }
      &:before {
        left: -14px;
      }
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1199px) {
  .stages {
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 30px;
    }
    &__item {
      width: 100%;
      margin: 0;
    }
  }
}

@media screen and (max-width: 1150px) {
  .equipment-estate {
    &__top {
      display: block;
    }
    &__title {
      max-width: 590px;
    }
    &__search {
      margin: 20px 0 0 0;
    }
    &__cards {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
  .equipment-technic {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
  .equipment-additionally {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
  .boards {
    &__inner {
      display: block;
    }
    &__left {
      justify-content: center;
    }
    &__right {
      margin: 25px 0 0 0;
    }
    &__cards {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 25px;
    }
    &__card {
      &:not(:first-child) {
        margin: 0;
      }
    }
  }
  .download {
    &__right {
      margin-left: 50px;
    }
  }
  .news {
    &__cards {
      gap: 25px;
    }
  }
  .order {
    &__inner {
      padding: 85px 45px 155px;
    }
  }
  .contacts {
    &__inner-wrap {
      max-width: 350px;
    }
  }
}

@media screen and (max-width: 992px) {
  .equipment-estate {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .equipment-technic {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .equipment-additionally {
    &__cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .boards {
    &__inner {
      display: flex;
      align-items: center;
    }
    &__left {
      display: block;
      width: calc(100% - 275px);
    }
    &__right {
      width: 225px;
      margin: 0 0 0 50px;
    }
    &__img {
      max-width: 400px;
    }
    &__desc {
      margin: 20px 0 0 0;
      max-width: 100%;
    }
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .about {
    &__top {
      display: block;
    }
    &__desc {
      max-width: 100%;
    }
    &__img {
      margin: 40px auto 0;
    }
    &__bottom {
      margin-top: 40px;
    }
    &__info {
      grid-template-columns: 1fr 1fr;
    }
  }
  .advantages {
    &__items {
      gap: 40px;
    }
  }
  .advantages2 {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
  .works {
    &__slide-content {
      display: block;
    }
    &__desc {
      margin: 40px 0 0 0;
    }
    .swiper-pagination {
      width: 100%;
      left: 0;
    }
  }
  .stages {
    padding: 50px 0 25px;
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
  .download {
    &__inner {
     flex-direction: column;
    }
    &__right {
      margin: 0 0 30px 0;
      order: -1;
    }
    &__btn {
      margin-left: 0;
    }
  }
  .news {
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
  }
  .order {
    &__img2 {
      display: none;
    }
  }
  .contacts {
    &__inner {
      &-wrap {
        max-width: 270px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    .swiper-slide {
      padding: 250px 0 20px;
      background-size: cover;
    }
    &__slide {
      &-content {
        padding: 20px 20px 50px;
      }
      &-title {
        font-size: 28px;
      }
      &-btn {
        height: 45px;
        font-size: 16px;
      }
    }
    .swiper-pagination {
      bottom: 32px;
    }
    .swiper-pagination-bullet {
      width: 14px;
      height: 14px;
    }
  }
  .equipment-estate {
    padding: 50px 0 30px 0;
    &__search {
      input {
        width: 100%;
      }
    }
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
    &__content {
      margin-top: 40px;
    }
  }
  .equipment-technic {
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
  }
  .equipment-additionally {
    padding: 30px 0 50px 0;
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
  }
  .boards {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 40px 0 0 0;
    }
    &__cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .about {
    padding: 50px 0 0 0;
    &__text {
      margin-top: 30px;
      font-size: 18px;
      p {
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__subtitle {
      font-size: 20px;
    }
    &__projects-link {
      font-size: 16px;
      margin-top: 20px;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
    &__info {
      gap: 25px;
      &-item {
        padding: 25px;
      }
      &-title {
        font-size: 32px;
      }
      &-text {
        font-size: 20px;
      }
    }
  }
  .advantages {
    &__title {
      font-size: 20px;
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 30px;
      margin-top: 20px;
    }
    &__item-text {
      font-size: 18px;
    }
    &__item-img {
      flex-shrink: 1;
      img {
        width: 20px;
        height: auto;
      }
    }
  }
  .advantages2 {
    padding: 50px 0;
    &__item {
      padding: 20px;
    }
  }
  .works {
    padding: 30px 0;
    &__slider {
      padding: 20px 20px 70px;
      margin-top: 25px;
      &:after {
        display: none;
      }
      &:before {
        display: none;
      }
    }
    .swiper-pagination-bullet {
      width: 14px;
      height: 14px;
    }
    .swiper-slide__btns {
      display: flex;
      align-items: center;
      margin-top: 50px;
    }
    .swiper-button-next {
      position: static;
      box-shadow: 0 1px 25px rgb(36 60 160 / 32%);
      margin-left: 10px;
    }
    .swiper-button-prev {
      position: static;
      box-shadow: 0 1px 25px rgb(36 60 160 / 32%);
    }
  }
  .stages {
    &__items {
      margin-top: 30px;
    }
    &__item {
      &-title {
        font-size: 20px;
        margin-top: 15px;
      }
      &-text {
        font-size: 16px;
        margin-top: 10px;
      }
    }
  }
  .hits {
    padding: 25px 0 100px;
    &__slide {
      margin-top: 15px;
    }
    .swiper-pagination {
      bottom: -50px;
    }
    .swiper-pagination-bullet {
      width: 14px;
      height: 14px;
    }
  }
  .download {
    padding: 30px 0;
    &__left {
      width: 100%;
    }
    &__text {
      font-size: 18px;
      margin-top: 15px;
    }
    &__btn {
      width: 300px;
      height: 60px;
      span {
        font-size: 18px;
      }
    }
  }
  .news {
    padding: 50px 0 100px;
    &__cards {
      margin-top: 25px;
    }
  }
  .order {
    padding: 50px 0;
    &__inner {
      padding: 30px 30px 100px;
    }
    &__img {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    .swiper-slide {
      padding: 200px 0 20px;
    }
    &__slide-title {
      font-size: 24px;
    }
    &__slide-btn {
      width: 100%;
    }
  }
  .equipment-estate {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .equipment-technic {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .equipment-additionally {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .boards {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .about {
    &__info {
      grid-template-columns: 1fr;
    }
  }
  .advantages2 {
    &__items {
      grid-template-columns: 1fr;
    }
  }
  .works {
    &__desc {
      margin-top: 20px;
    }
    &__btns {
      margin-top: 30px;
    }
    &__btn {
      width: 100%;
    }
  }
  .stages {
    &__bg {
      bottom: -250px;
    }
    &__items {
      grid-template-columns: 1fr;
    }
  }
  .download {
    &__btn {
      width: 100%;
      span {
        font-size: 16px;
        margin-right: 15px;
      }
    }
  }
  .news {
    &__top {
      display: block;
    }
    &__btn {
      margin-top: 15px;
    }
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .order {
    &__inner {
      padding: 30px 15px 100px;
    }
  }
  .contacts {
    &__inner-wrap {
      max-width: 200px;
      padding: 20px 15px;
    }
    &__socials {
      margin-top: 20px;
    }
    &__social {
      span {
        font-size: 14px;
      }
    }
  }
}