.header {
  padding: 25px 0 35px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    margin-left: 12px;
  }
  &__logo {
    display: block;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__connection {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__links {
    display: flex;
    align-items: center;
    border-bottom: 0.5px solid #243CA0;
    padding-bottom: 19px;
  }
  &__mail {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    &:hover {
      span {
        &:after {
          background: #243ca0;
        }
      }
    }
    span {
      margin-left: 10px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.5px;
        background: #000;
      }
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    margin-left: 55px;
    span {
      margin-left: 10px;
    }
  }
  &__search {
    margin-left: 55px;
    margin-top: -19px;
    &-inner {
      position: relative;
      input {
        width: 215px;
        height: 40px;
        background: #FFFFFF;
        border: 1px solid #243CA0;
        box-shadow: inset 1px -2px 9px rgba(0, 0, 0, 0.1);
        border-radius: 3px;
        font-size: 16px;
        line-height: 120%;
        color: rgba(36, 60, 160, 1);
        padding: 0 10px 0 35px;
        &::placeholder {
          color: rgba(36, 60, 160, 0.4);
        }
      }
      img {
        position: absolute;
        top: 50%;
        left: 10px;
        transform: translate(0, -50%);
        pointer-events: none;
      }
    }
  }
  &__btn-search {
    background: url("../img/search.svg");
    background-repeat: no-repeat;
    background-position: center;
    width: 18px;
    height: 18px;
    margin-left: 25px;
    position: relative;
    z-index: 5;
    cursor: pointer;
    display: none;
    transition: all 0.5s;
  }
  &__menu {
    padding-top: 19px;
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 18px;
      line-height: 120%;
      letter-spacing: -0.025em;
      &:not(:first-child) {
        margin-left: 45px;
      }
    }
  }
  &__btn {
    font-size: 18px;
    letter-spacing: -0.025em;
    background: transparent;
    position: relative;
    margin-left: 45px;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 0.5px;
      background: #000;
    }
  }
  &__burger {
    width: 32px;
    height: 24px;
    position: relative;
    z-index: 4;
    cursor: pointer;
    margin-left: 25px;
    display: none;
    span {
      height: 3px;
      background: #243CA0;
      transition: all 0.5s;
      &:nth-child(1) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
      }
      &:nth-child(2) {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
      }
      &:nth-child(3) {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
      }
    }
  }
  &__catalog {
    position: relative;
    cursor: pointer;
    &--active {
      .header__catalog-hidden {
        display: block;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 2;
      left: -5px;
      top: 35px;
      width: 100%;
     display: none;
    }
    &-link {
      font-size: 16px;
      line-height: 120%;
      position: relative;
      span {
        &:hover {
          border-bottom:0.5px solid #243CA0;
        }
      }
      &--active {
        span {
          border-bottom:0.5px solid #243CA0;
        }
      }
    }
  }
  &__hidden-inner {
    width: 662px;
    background: #FFFFFF;
    box-shadow: 0px 1px 25px rgba(36, 60, 160, 0.32);
    margin: 0 auto;
    padding: 30px 20px 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px 75px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: -12px;
      left: 0;
      border-left: 70px solid transparent;
      border-right: 70px solid transparent;
      border-bottom: 20px solid #fff;

    }
  }
}

