@media screen and (max-width: 1150px) {
  .catalog {
    &__cards {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    padding: 0 0 50px;
    &__cards {
      gap: 30px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
}