.catalog-item {
  padding: 0 0 40px 0;
  &__inner {
    display: grid;
    grid-template-columns: 29% auto;
    gap: 80px;
  }
  &__right {
    padding-top: 80px;
  }
  .gallery__big {
    position: relative;
  }
  .gallery__small {
    margin-top: 10px;
    .swiper-slide {
      height: 60px;
      width: 60px;
    }
  }
  .swiper-slide-thumb-active {
    border: 1px solid #5C63FF;
  }
  .swiper-slide {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__gallery {
    width: 100%;
    height: 100%;
    position: relative;
    padding-bottom: 100%;
    display: block;
    img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__price {
    font-weight: 500;
    font-size: 24px;
    line-height: 108.2%;
    letter-spacing: -0.025em;
    color: #243CA0;
    margin-top: 28px;
  }
  &__btns-wrap {
    position: relative;
    margin-top: 25px;
  }
  &__btn {
    width: 154px;
    height: 40px;
    padding-left: 45px;
    position: relative;
    text-transform: uppercase;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 52px;
      transform: translate(0, -50%);
      width: 1px;
      height: 19px;
      background: rgba(255, 255, 255, 0.58);
    }
  }
  &__calc {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    input {
      width: 15px;
      height: 15px;
      font-size: 16px;
      border: none;
      background: transparent;
      color: #FFFFFF;
    }
    &-plus {
      background: transparent;
      color: #FFFFFF;
    }
    &-minus {
      background: transparent;
      color: #FFFFFF;
    }
  }
}
.characteristic {
  padding: 40px 0 0;
  &__desc {
   p {
     font-size: 20px;
     line-height: 130%;
     letter-spacing: -0.025em;
     margin-top: 35px;
   }
    p:not(:first-child) {
      margin-top: 30px;
    }
  }
}
.directions {
  padding: 0 0 130px;
  &__inner {
    max-width: 965px;
    margin: 0 auto;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 5px;
    margin-top: 35px;
  }
  &__item {
    display: grid;
    grid-template-rows: 60% 40%;
    gap: 5px;
    &2 {
      display: grid;
      grid-template-rows: 53% 47%;
      gap: 5px;
    }
    &3 {
      display: grid;
      grid-template-rows: 47% 53%;
      gap: 5px;
    }
    &4 {
      display: grid;
      grid-template-rows: 40% 60%;
      gap: 5px;
    }
  }
  &__card {
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
     color: #FFFFFF;
    }
    &-blue {
      padding: 65px 15px;
      background: #1872AE;
    }
    &-blue2 {
      padding: 30px 15px;
      background: #049DB9;
    }
    &-blue3 {
      padding: 36px 15px;
      background: #3986A4;
    }
    &-blue4 {
      padding: 44px 15px;
      background: #4A6C79;
    }
    &-blue5 {
      padding: 28px 15px;
      background: #40AFD7;
    }
    &-blue6 {
      padding: 34px 15px;
      background: #4CB4BB;
    }
    &-yellow {
      background: #AFB745;
      padding: 39px 15px;
    }
    &-red {
      padding: 42.5px 15px;
      background: #DC1720;
    }
    &-title {
      font-size: 14px;
      margin-right: 5px;
    }
  }
}