@media screen and (max-width: 1150px) {
  .form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    align-items: start;
    &-group {
      &:not(:first-child) {
        margin-left: 0;
      }
    }
    &-group__wrap {
      width: 100%;
    }
    &-group__wrap-textarea{
      width: 100%;
    }
    &__btn {
      position: absolute;
      left: 45px;
      bottom: 45px;
    }
  }
}

@media screen and (max-width: 992px) {
  .form {
    grid-template-columns: 1fr;
    max-width: 600px;
    margin: 0 auto;
    &__btn {
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 24px;
  }
  .equipment-card {
    &__img {
      height: 160px;
    }
  }
  .hits-card {
    padding: 45px 15px 15px;
    &__icons {
      opacity: 1;
    }
    &__title {
      font-size: 16px;
    }
    &__price {
      font-size: 20px;
    }
    &__btns {
      display: block;
      &-wrap {
        margin-top: 15px;
      }
    }
    &__btn,
    &__btn2 {
      width: 100%;
    }
    &__btn2 {
      padding-left: 0;
    }
  }
  .form {
    &__text {
      font-size: 18px;
      margin-top: 15px;
    }
    &-group__wrap {
      input {
        font-size: 16px;
        height: 45px;
      }
    }
    &-group__wrap-textarea {
     textarea {
       font-size: 16px;
       height: 150px;
     }
    }
    &__btn {
      height: 45px;
      bottom: 30px;
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 576px) {
  .form {
    &__btn {
      width: calc(100% - 30px);
    }
  }
}