.footer {
  background: #1A318C;
  padding: 40px 0 25px;
  a {
    &:hover {
      color: #c4c4c4;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
  }
  &__logo {
    display: block;
    width: 150px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 120%;
      letter-spacing: -0.03em;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__social {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 15px;
    }
    span {
      font-size: 16px;
      line-height: 120%;
      letter-spacing: -0.03em;
      margin-left: 5px;
    }
  }
}